export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			date: '请输入您的生日!',
			date1: currentDate,
			array: ['男', '女'],
			index: 0,
			Piclist:true,
			Imglist:true,
			addpic:[],
		}
	},
	methods: {
		bindPickerChange: function(e) {
			this.index = e.target.value
		},
		bindDateChange: function(e) {
			var oDate1 = new Date(this.date1);
			var oDate2 = new Date(e.target.value);
			console.log(oDate2)
			console.log(oDate2.getTime)
			if (oDate1.getTime() > oDate2.getTime()) {
				this.date = e.target.value
			} else {
				uni.showModal({
					title: '提示',
					content: '生日不能大于或等于当前日期!',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}

		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		Addimg:function(e){
			uni.chooseImage({ 
			    count: 1, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album','camera'], //从相册选择
			    success:(res)=>{  
					var tempFilePaths = res.tempFilePaths
					this.addpic = this.addpic.concat(tempFilePaths)
					if(this.addpic.length == 3){
						this.Piclist = !this.Piclist
					}
			    }
			})
		},
		deleteImage:function(index){
			uni.showModal({
				title: '提示',
				content: '确定要删除此图片吗？',
				success:(res)=>{
		        	console.log(index)
					this.addpic = this.addpic.splice(index,1)
					if(this.addpic.length < 3){
						this.Piclist = !this.Piclist
					}
				}
			})
		}
	}
}
